.sidebar {
    top: 80px;
    position: sticky;
    flex: .2;
    /* 20% of the width of div */
    border-radius: 10px;
    text-align: center;
    height: fit-content;

}

.sidebar_top>h4 {
    color: gray;
    font-size: 12px;
    font-weight: bold;
}

.sidebar_top>h2 {
    font-size: 14px;

}

.sidebar_top {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightgray;
    border-bottom: 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    background-color: white;
    padding-bottom: 10px;
}

.sidebar_avatar {
    margin-bottom: 10px;
}

.sidebar_top>img {
    margin-bottom: -20px;
    width: 100%;
    height: 60px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    object-fit: cover;
}

.sidebar_stat {
    margin-top: 0px;
    display: flex;
    justify-content: space-around;
}

.sidebar_stat>p {
    color: gray;
    font-size: 13px;
    font-weight: 600;

}

.sidebar_statNumber {
    font-weight: bold;
    color: #0a66c2 !important;
}

.sidebar_stats {
    padding: 10px;
    margin-bottom: 10px;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
}

.sidebar_button {
    text-align: left;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 10px;
    margin-top: 10px;
}

.sidebar_button>p {
    font-size: 13px;
    padding-bottom: 10px;
    font-weight: bold;
}

.sidebar_recentItem {
    display: flex;
    font-size: 13px;
    color: gray;
    cursor: pointer;
    font-weight: bolder;
    margin-bottom: 5px;
    padding: 5px;
}

.sidebar_recentItem:hover {
    background-color: whitesmoke;
    border-radius: 10px;
    cursor: pointer;
    color: black;
}

.sidebar_hash {
    margin-right: 10px;
    margin-left: 5px;
}